import React, { Component } from "react";
import TutorialDataService from "../services/tutorial.service";
import { Link } from "react-router-dom";

export default class Brands extends Component {

  constructor(props) {
    super(props);
    this.state = {
      brandsData: [],
      DataisLoaded: false
    };
    this.retrieveTutorials = this.retrieveTutorials.bind(this);
  }

  retrieveTutorials() {
    TutorialDataService.getBrandsNew()
      .then(response => {
        return response.json();
      })
      .then(response => {
        console.log(response);
        this.setState({
          brandsData: response.brands
        });
      })
      .catch(e => {
        console.log(e);
      });
  }

  componentDidMount() {
    this.retrieveTutorials();
  }
  
  render() {

    const brands = this.state.brandsData;

    return (
      <>
        <div className="card-group">
        {brands.map((brand,index) => {
          return (
            <div className="card bg-light" key={index}>
              <img className="card-img-top" src={brand.image} alt={brand.name} style={{width:"100%"}} />
              <div className="card-body text-center">
                <h4 className="card-title">{brand.name}</h4>
                <Link to={`/products`} className="stretched-link">
                </Link>
              </div>
            </div>
          );
        })}
        </div>
      </>
    );
  }
}
