import React, { Component } from "react";
import TutorialDataService from "../services/tutorial.service";
import { Link } from "react-router-dom";
export default class CartItems extends Component {

  constructor(props) {
    super(props);
    this.state = {
      cartData: [],
      DataisLoaded: false
    };
    this.retrieveTutorials = this.retrieveTutorials.bind(this);
  }

  retrieveTutorials() {
    TutorialDataService.getcartItemsNew()
      .then(response => {
        return response.json();
      })
      .then(response => {
        this.setState({
          cartData: response.cartitems
        });
      })
      .catch(e => {
        console.log(e);
      });
  }

  componentDidMount() {
    this.retrieveTutorials();
  }
  
  render() {

    const cart = this.state.cartData;

    return (
      <>
        <div className="card-group">
        {cart.map((brand,index) => {
          return (
            <div className="card bg-light" key={index}>
              <img className="card-img-top" src={brand.product_image} alt={brand.product_name} style={{width:"100%"}} />
              <div className="card-body text-center">
                <h4 className="card-title">{brand.product_name}</h4>
              </div>
            </div>
          );
        })}
        </div>
        <button><Link to={`/checkout`} className="stretched-link" style={{
          textDecoration: "none",color: "#000"
        }}>PROCEED TO CHECKOUT</Link></button>
      </>
    );
  }
}
