import http from "../http-common";
import { v4 as uuid } from 'uuid';

class TutorialDataService {
  
  setNewRelicCustomAttr() {
    if(typeof newrelic !== 'undefined') {
      window.newrelic.setCustomAttribute("my-request-id", uuid())
    }
  }

  getAll() {
    // this.setNewRelicCustomAttr();
    return http.get("/tutorials");
  }

  get(id) {
    // this.setNewRelicCustomAttr();
    return http.get(`/tutorials/${id}`);
  }

  create(data) {
    // this.setNewRelicCustomAttr();
    return http.post("/tutorials", data);
  }

  update(id, data) {
    // this.setNewRelicCustomAttr();
    return http.put(`/tutorials/${id}`, data);
  }

  delete(id) {
    // this.setNewRelicCustomAttr();
    return http.delete(`/tutorials/${id}`);
  }

  deleteAll() {
    // this.setNewRelicCustomAttr();
    return http.delete(`/tutorials`);
  }

  findByTitle(title) {
    this.setNewRelicCustomAttr();
    return http.get(`/tutorials?title=${title}`);
  }

  getBrands(){
    return http.get("/brands");
  }

  getproducts(id){
    return http.get(`/products/${id}`);
  }

  getcartItems(){
    return http.get("/cartitems");
  }

  addToCart(data){
    return http.post("/addtocart",data);
  }

  getBrandsNew(){
    return fetch('http://20.241.170.127:4000/')
  }

  getproductsNew(){
    return fetch(`http://20.62.155.33:4002/`)
  }
  
  getcartItemsNew(){
    return fetch(`http://20.72.139.188:4001/`)
  }
  addToCartNew(value){
    return fetch(`http://20.72.139.188:4001/`,{
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        id:value
      })
    })
  }
  login(value){
    return fetch(`http://20.241.204.68:4003/`,{
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(value)
    })
  }

  checkout(value){
    return fetch(`http://20.241.205.55:4004/`,{
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(value)
    })
  }
}

export default new TutorialDataService();