import React, { Component } from "react";
import TutorialDataService from "../services/tutorial.service";
import withRouter from './withRouter';

class Products extends Component {
  
  constructor(props) {
    super(props);
    this.state = {
      productsData: [],
      message:''
    };
    this.retrieveTutorials = this.retrieveTutorials.bind(this);
  }

  retrieveTutorials() {
    
    TutorialDataService.getproductsNew()
      .then(response => {
        return response.json();
      })
      .then(response => {
        this.setState({
          productsData: response.products
        });
      })
      .catch(e => {
        console.log(e);
      });
  }

  componentDidMount() {
    // let { id } = this.props.params;
    this.retrieveTutorials();
  }

  addToCart = (data)=>{
    console.log(data);
    TutorialDataService.addToCartNew(data)
      .then(response => {
        return response.json();
      })
      .then(response => {
        this.setState({
          message: response.msg
        });
      })
      .catch(e => {
        console.log(e);
      });
  }

  render() {
    const products = this.state.productsData;

    return (
      <>
        <div className="card-group">
        {products.map((product,index) => {
          return (
            // <div className="card bg-light" key={index} data-bs-toggle="modal" data-bs-target="#exampleModal" onClick={()=>this.handleModal(product)}>
            <div className="card bg-light" key={index}>
              <img className="card-img-top" src={product.product_image} alt={product.product_name} style={{width:"100%"}} />
              <div className="card-body text-center">
                <h4 className="card-title">{product.product_name}</h4>
                <p className="card-text">{product.product_description}</p>
              </div>
              <button onClick={()=>this.addToCart(product.id)}>ADD TO CART</button>
            </div>
          );
          
        })}
        </div>
        {this.state.message?
          <div style={{
            background: "#777",
            textAlign: "center",
            color: "#FFF",
            margin: "10px 0px",
            padding: "10px 10px"
          }}>{this.state.message.toUpperCase()}</div>
        :null}
      </>
    );
  }
}
export default withRouter(Products);