import React, { Component } from "react";
import { Routes, Route, Link } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";

import AddTutorial from "./components/add-tutorial.component";
import Tutorial from "./components/tutorial.component";
import TutorialsList from "./components/tutorials-list.component";
import Brands from "./components/brands.component";
import Products from "./components/products.component";
import Cart from "./components/cartItems.component"
import Login from "./components/login.component";
import Checkout from "./components/checkout.component";
class App extends Component {
  render() {
    return (
      <div>
        <nav className="navbar navbar-expand navbar-dark bg-dark">
          <div className="container-fluid">  
            <Link to={"/tutorials"} className="navbar-brand">
              CloudEQ
            </Link>
            <div className="navbar-nav mr-auto">
              <li className="nav-item">
                <Link to={"/tutorials"} className="nav-link">
                  Tutorials
                </Link>
              </li>
              <li className="nav-item">
                <Link to={"/add"} className="nav-link">
                  Add
                </Link>
              </li>
              <li className="nav-item">
                <Link to={"/brands"} className="nav-link">
                  Brands
                </Link>
              </li>
              <li className="nav-item">
                <Link to={"/cart-items"} className="nav-link">
                  Cart
                </Link>
              </li>
              <li className="nav-item">
                <Link to={"/login"} className="nav-link">
                  Login
                </Link>
              </li>
            </div>
          </div>
        </nav>

        <div className="container mt-3">
            <Routes>
              <Route exact path="/" element={<TutorialsList />} />
              <Route exact path="/tutorials" element={<TutorialsList />} />
              <Route exact path="/add" element={<AddTutorial />} />
              <Route exact path="/tutorials/:id" element={<Tutorial />} />
              <Route exact path="/brands" element={<Brands />} />
              <Route exact path="/products" element={<Products />} />
              <Route exact path="/cart-items" element={<Cart />} />
              <Route exact path="/login" element={<Login />} />
              <Route exact path="/checkout" element={<Checkout />} />
            </Routes>
        </div>
      </div>
    );
  }
}

export default App;
